// typing effect Closure
// typingEffect(test)(args)
const titleClass = 'main-title'
const bodyClass = 'main-body'

const typingEffect = (text, object, positionSet, cssClass) => {
    const textLength = text.length
    const element = document.createElement( 'div' );
    element.textContent = ' ';
    let i = 0
    const typingEffectEachElement = () => {
        const [x, y, z] = positionSet;
        if(i<textLength){
            const divLabel = new CSS2DObject( element );
            element.className = cssClass;
            element.textContent += text.charAt(i)
            divLabel.position.set(...positionSet);
            object.add( divLabel );
            i++
            setTimeout(typingEffectEachElement, 3000)
        }
    }
    return typingEffectEachElement
}

//video
function videoElement( id, positionSet ) {
    const [x,y,z]=positionSet;
  
    const element = document.createElement( 'div' );
    element.className = 'video-container-class';
  
    const iframe = document.createElement( 'iframe' );
    iframe.className = 'video-class';
    iframe.src = [ 'https://www.youtube.com/embed/', id, '?autoplay=1' ].join( '' );
    element.appendChild( iframe );
  
    const object = new CSS2DObject( element );
    object.position.set( ...positionSet );
  
    return object;
  };

