import './style.css'
import * as THREE from 'three'
import * as dat from 'lil-gui'
import { CSS2DRenderer } from 'three/examples/jsm/renderers/CSS2DRenderer.js'
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader'
import * as environment from './environment.js'
import * as animation from './mouse-animation.js'
import * as objects from './objects.js'

const scene = new THREE.Scene()
scene.background = new THREE.Color('#000000')
const sizes = { width: window.innerWidth, height: window.innerHeight}
let labelRenderer

const renderer = new THREE.WebGLRenderer();
renderer.setSize( sizes.width, sizes.height );
document.body.appendChild( renderer.domElement );

labelRenderer = new CSS2DRenderer();
labelRenderer.setSize( window.innerWidth, window.innerHeight );
labelRenderer.domElement.style.position = 'absolute'; 
labelRenderer.domElement.style.top = '0px';
labelRenderer.domElement.style.overflowX = 'hidden';
document.body.appendChild( labelRenderer.domElement );
document.cookie = "crossCookie=bar; SameSite=None; Secure"

const mouse = new THREE.Vector2()
const camera = new THREE.PerspectiveCamera(
    75,
    sizes.width / sizes.height,
    0.1,
    1000
)
camera.position.set(-32.5,21.5,12)

let cameraPosition = camera.position
let doesMusicPlaying = false
let loadedBarLengthPercent = 0

const websiteUrl = 'https://www.waynehills.co/'
const loadingScreen = document.getElementById( 'loading-background' );
const loadingVideo = document.getElementById( 'loading-video' );
const loadingProcessFill = document.getElementById('loading-progress-fill')
const buttonContainer = document.getElementById('loading-button')
const buttonBackground = document.getElementById('button-background')
const buttonText = document.getElementById('button-text')
const backButtonContainer = document.getElementById('to-beginning')
const websiteButtonContainer = document.getElementById('to-website')
const resetButton = document.getElementById('reset-camera')

objects.loadingManager.onProgress = function(item, loaded, total){
    loadedBarLengthPercent = (loaded*394)/total
    loadingProcessFill.setAttribute('width', String(loadedBarLengthPercent))
};
objects.loadingManager.onLoad = function(){
    buttonBackground.setAttribute("fill", "#313E53")
    buttonBackground.setAttribute("fill-opacity", "1.0")
    buttonText.innerHTML = 'Start Voyage!'
    buttonContainer.onclick = buttonClick
};

const textureLoader = new THREE.TextureLoader(objects.loadingManager)
const rocketTexture = textureLoader.load('images/rocket.png')
const rocketGeometry = new THREE.PlaneGeometry(1.5,1.5)
const rocketMaterial = new THREE.MeshBasicMaterial({
    map: rocketTexture,
    transparent: true
})
const rocket = new THREE.Mesh(rocketGeometry, rocketMaterial);
rocket.position.set(-32.5,21.5,3)
rocket.rotation.set(0,0,0)
scene.add(rocket)

let rocketRotation = rocket.rotation

