const screenType =()=>{
    if (319 < window.innerWidth && window.innerWidth <= 719) {
        //xxs: 410
        cameraPosition.z=24
    } else if (719 < window.innerWidth && window.innerWidth <= 799) {
        //xs: 720
        cameraPosition.z=18
    }else if (799 < window.innerWidth && window.innerWidth <= 1439) {
        //sm: 800
        cameraPosition.z=17
    } else if (1439 < window.innerWidth && window.innerWidth <= 1919) {
        //md: 1440
        cameraPosition.z=12
    } else if (1919 < window.innerWidth && window.innerWidth <= 2304) {
        //lg: 1920
        cameraPosition.z=11
    } else if (2304 < window.innerWidth && window.innerWidth <= 2880) {
        //xl: 2560
        cameraPosition.z=10
    } else if (2880 < window.innerWidth) {
        //xxl: 3840
        cameraPosition.z=8
    }else {
        //etc
        cameraPosition.z=12
    }
}

screenType()

function onWindowResize() {
    camera.aspect = window.innerWidth / window.innerHeight;
    camera.updateProjectionMatrix()

    renderer.setSize( window.innerWidth, window.innerHeight )
    labelRenderer.setSize( window.innerWidth, window.innerHeight );
    screenType()
    render()
}

function move(evt){
    mouse.x = (evt.touches) ? evt.touches[0].clientX : evt.clientX;
    mouse.y = (evt.touches) ? evt.touches[0].clientY : evt.clientY;
}

function playMusic(){
    if(doesMusicPlaying != true){
        environment.backgroundMusic(audioLoader, backgroundSound)
        doesMusicPlaying = true
    }
}

const buttonClick = ()=>{
    playMusic()
    loadingScreen.style.display='none'
    loadingVideo.pause()
    loadingVideo.currentTime = 0;
    window.addEventListener("wheel", animation.onMouseWheel)
    window.addEventListener("touchstart", animation.touchStart);
    window.addEventListener("touchend", animation.touchEnd);
    animation.scrollAmountReset(cameraPosition)
    resetButton.onclick = resetClick
}

const directWebsite = ()=>{
    window.open(websiteUrl, "_blank");
}
backButtonContainer.onclick = buttonClick
websiteButtonContainer.onclick = directWebsite

const resetClick =()=>{
    animation.scrollAmountReset(cameraPosition)
}

if ('ontouchstart' in window){
    window.addEventListener('touchmove', move);
    }else{
    // window.addEventListener( 'resize', onWindowResize, false );
    window.addEventListener('mousemove', (event) => {
        mouse.x = event.clientX / sizes.width *2-1
        mouse.y = -(event.clientY / sizes.height) *2+1 
    })
}
window.addEventListener('resize', onWindowResize, false)

