// cameraFolder.add(cameraRotation, 'z', -100, 100, 0.001).name('rotationZ')

const listener = new THREE.AudioListener();
camera.add( listener );
const backgroundSound = new THREE.PositionalAudio( listener );
const audioLoader = new THREE.AudioLoader(objects.loadingManager);

environment.baseElement(scene)
environment.generateBackground(scene)
environment.generateParticles(scene)
objects.sceneAdd(scene)

const clock = new THREE.Clock()
let previousTime = 0

function animate() {
    const elapsedTime = clock.getElapsedTime()
    const deltaTime = elapsedTime - previousTime
    previousTime = elapsedTime

    // if(mixer){
    //     mixer.update(deltaTime)
    // }

    rocket.position.x = camera.position.x
    rocket.position.y = camera.position.y-2
    
    animation.mousePositionUpdate()
    animation.mousePositionReset()
    animation.cameraPositionUpdate(cameraPosition, rocketRotation)
    // objects.animateObjects()

    requestAnimationFrame(animate)
    render()
}

function render() {
    renderer.render(scene, camera)
    labelRenderer.render( scene, camera );
}

animate()